lor: $async-color;\r\n  }\r\n}\r\n\r\n.snotify-confirm {\r\n  background-color: $confirm-bg;\r\n  .snotifyToast__progressBar {\r\n    background-color: $confirm-progressBar;\r\n  }\r\n  .snotifyToast__progressBar__percentage {\r\n    background-color: $confirm-progressBarPercentage;\r\n  }\r\n  .snotifyToast__body {\r\n    color: $confirm-color;\r\n  }\r\n}\r\n\r\n.snotify-prompt {\r\n  background-color: $prompt-bg;\r\n  ng-snotify-prompt {\r\n    width: 100%;\r\n  }\r\n  .snotifyToast__title {\r\n    margin-bottom: 0;\r\n  }\r\n  .snotifyToast__body {\r\n    color: $prompt-color;\r\n  }\r\n}\r\n\r\n.snotify-confirm,\r\n.snotify-prompt {\r\n  .snotifyToast__inner {\r\n    padding: 10px 15px;\r\n  }\r\n}\r\n",".snotifyToast__input {\r\n  position: relative;\r\n  z-index: 1;\r\n  display: inline-block;\r\n  margin: 0;\r\n  width: 100%;\r\n  vertical-align: top;\r\n  transition: all 0.5s;\r\n  transition-delay: 0.3s;\r\n  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);\r\n\r\n  &__field {\r\n    position: relative;\r\n    display: block;\r\n    float: right;\r\n    padding: 0.85em 0.5em;\r\n    width: 100%;\r\n    border: none;\r\n    border-radius: 0;\r\n    background: transparent;\r\n    color: #333;\r\n    font-weight: bold;\r\n    -webkit-appearance: none; /* for box shadows to show on iOS */\r\n    opacity: 0;\r\n    transition: opacity 0.3s;\r\n\r\n    &:focus {\r\n      outline: none;\r\n    }\r\n  }\r\n\r\n  &__label {\r\n    display: inline-block;\r\n    float: right;\r\n    padding: 0 0.85em;\r\n    width: 100%;\r\n    color: #e0f2f1;\r\n    font-weight: bold;\r\n    font-size: 70.25%;\r\n    -webkit-font-smoothing: antialiased;\r\n    -moz-osx-font-smoothing: grayscale;\r\n    -webkit-touch-callout: none;\r\n    -webkit-user-select: none;\r\n    -khtml-user-select: none;\r\n    -moz-user-select: none;\r\n    -ms-user-select: none;\r\n    user-select: none;\r\n    position: absolute;\r\n    left: 0;\r\n    height: 100%;\r\n    text-align: left;\r\n